.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

.wrapper {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  background-color: rgb(255, 255, 255);
}

@font-face {
  font-family: "Averta";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Averta.woff2") format("woff2");
  font-display: swap;
}
body {
  font-family: "Averta", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  font-family: "Averta", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
  width: 100%;
  margin: 0;
  background-color: #FEF4FF;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
  user-select: none;
}

fieldset {
  border: none;
}

a {
